.b2b .header__menu .main-menu__item .mobile {
  @media #{$medium-up} {
    display: none;
  }
}

.b2b .pushy .main-menu__item {
  &:last-child {
    height: $topbar-height;
    line-height: $topbar-height;
    text-align: left;
  }

  .btn-rounded {
    display: none;

    @media #{$medium-up} {
      display: block;
    }
  }
}

.stick-info {
  @include display(flex);
  height: 100%;
  overflow: hidden;
  
  &.hidden {
    display: none;
  }
  
  &--left {
  }
  
  &--right {
  }

  &--buttons {
    @include display(flex);
    @include justify-content(flex-end);

    .btn-primary,
    .btn-secondary {
      margin-bottom: 0;
      margin-right: rem-calc(16);

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &--summary {
    &--title {
      font-weight: bold;
    }
    
    &--price {
      font-size: rem-calc(16);
      margin: 0;
    }

    table {
      border: 0;
      margin: 0;
    }

    td { 
      padding: 0;
    }

    .tag,
    .tva {
      //font-size: rem-calc(10);
    }
  }


  &--contacts {
    background: transparent;
    font-size: rem-calc(15);
    margin: 0;
    padding: 0;

    .programContactInformation__content {
      @include justify-content(flex-end);
    }

    .programContactInformation__heading {
      display: none;
    }

    .programContactInformation__item {
      margin: 0;
      padding: 0 rem-calc(16) 0 0;
      width: auto;
      
      &:last-child {
        padding: 0 0 0 rem-calc(16);
      }
    }

    .programContactInformation__mail {
      line-height: 1;
      margin-bottom: rem-calc(6);
    }

    .programContactInformation__phone {
      display: block;
      line-height: rem-calc(12);

      &:last-child:before {
        content: '';
        margin-left: rem-calc(27);
      }

      .icon {
        @include transform(rotate(45deg));
      }
    }

    .programContactInformation__phone,
    .programContactInformation__mail {
      .icon {
        font-size: rem-calc(20);
        line-height: rem-calc(12);
      }
    }
  }

  &--summary--title {
    max-width: rem-calc(240);
  }

  .programContactInformation__title {
    max-width: rem-calc(160);
  }
  
  .programContactInformation__title,
  &--summary--title {
    font-size: rem-calc(15);
    line-height: 1.2;
  }
}


